import React, { Component, useState } from 'react';
import './FixedLocations.css'
import { LOCATIONS } from '../../shared/locations'

let locCount = [0] 

class FixedLocations extends Component {

    constructor(props){
        super(props);

        this.state={
            id:'',
            location:'',
            fixed_locations:LOCATIONS
        }

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        // this.addClass = this.addClass.bind(this);
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
    
        this.setState({
          [name]: value
        });
    }

    handleSubmit(event) {
        let locLength = LOCATIONS.length;
        const obj = { 'id': locLength , 'location': this.state.location}
        this.setState({
            fixed_locations:[...this.state.fixed_locations,obj]
        })
        LOCATIONS.push(obj)
        this.setState({
            location:''
        })
        event.preventDefault();
    }


  

//   addClass = (e) => {
//     if(locCount[e.target.id] == undefined || locCount[e.target.id] == 0){
//       e.target.className += ' preference__modal__roles__role__selected'
//       locCount[e.target.id] = 1
//     }
//     else{
//       e.target.className = 'preference__modal__roles__role'
//       locCount[e.target.id] = 0
//     }
//   };



    
    render() {
        return (
            <div className="input-row">
                <form className="form-inline" onSubmit={this.handleSubmit}>
                    <div className="row">
                        <div className="col">
                            <input type="text" className="form-control" placeholder="Locations"
                                name="location"
                                value={this.state.location}
                                onChange={this.handleInputChange}
                            />
                        </div>
                        <div className="col">
                            <button type="submit" className="btn btn-primary ">Add Locations</button>
                        </div>
                    </div>
                </form>

                <div className="display-tags">
                    {
                        this.state.fixed_locations.map((loc) =>{
                            return(
                                <p key={loc.id}  className="preference__modal__roles__role__selected">{loc.location}</p>
                            );
                        })
                    }
                </div>
            </div>
        );
    }
}

export default FixedLocations;